import ui from './util/ui';
import exportUtils from './util/export';
import openExportDialog from "./export/open-export-dialog";

export default function(context) {

  openExportDialog(context, {
    title: 'Create Combined Mixins',
    informativeText: 'Group the initial mixins into super mixins with media queries',
    confirmBtnText: 'Export SCSS'
  }, (textStyles, data) => {

    // Create mega mixins and save out
    let output = exportUtils.createCombinedStyles(textStyles, data);

    // Ask the user to save the file
    ui.createSavePanel('_combined-type.scss', output);
  });
};